.Form{
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 100px;
    .title{
    text-align: center;
    font-weight: 400;
    font-size: 30px;
    span{
      color: #4b44ff;
      font-weight: bold;
    }
}
a{
  display: block;
  width: 100%;
  padding: 15px 0;
  border: none;
  background-image: linear-gradient(to top, #4b44ff, #34bff3);
  color: white;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  text-align: center;
}
@media screen and (max-width:1000px){
  margin: auto;
  width: 90%;
padding: 0 20px;
}
}

